.bottom-scene-container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    padding: 25px 0 0 0;
}

.bottom-title {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    font-family: 'Montserrat', sans-serif;
    color: white;
    font-size: 42px;
    text-align: center;
    padding: 0;
    left: 0;
    z-index: 99;
    top: 0;
    margin: 0 20px 0 0;
}

.no-canvas {
    padding: 160px 0 160px 0;
}

.with-canvas {
    margin-bottom: 50px;
}

#c2 {
    vertical-align: middle;
    margin: 0;
    display: inline-block;
    position: relative;
}

@media screen and (max-width: 1280px) {
    .bottom-title {
        position: absolute;
        top: 50%;
        display: block;
        left: 50%;
        font-size: 36px;
        transform: translate(-50%, -50%);
        margin: 0;
        width: 90%;
    }
}

@media screen and (max-width: 1024px) {
    .bottom-title {
        font-size: 32px;
    }

    .with-canvas {
        margin-bottom: 40px;
    }

    .no-canvas {
        padding: 140px 0 140px 0;
    }
}

@media screen and (max-width: 768px) {
    .bottom-title {
        font-size: 28px;
    }

    .with-canvas {
        margin-bottom: 30px;
    }

    .no-canvas {
        padding: 120px 0 120px 0;
    }
}

@media screen and (max-width: 445px) {
    .bottom-title {
        font-size: 24px;
    }

    .with-canvas {
        margin-bottom: 20px;
    }

    .no-canvas {
        padding: 100px 0 100px 0;
    }
}

@media screen and (max-width: 320px) {
    .bottom-title {
        font-size: 20px;
    }

    .with-canvas {
        margin-bottom: 10px;
    }

    .no-canvas {
        padding: 80px 0 80px 0;
    }
}

@media screen and (max-height: 750px) and (min-width: 768px) {
    .bottom-title {
        font-size: 28px;
    }
}

@media screen and (max-height: 600px) and (min-width: 768px) {
    .bottom-title {
        font-size: 24px;
    }
}

@media screen and (max-height: 450px) and (min-width: 768px) {
    .bottom-title {
        font-size: 20px;
    }
}

@media screen and (max-height: 750px) and (min-width: 425px) and (max-width: 768px) {
    .bottom-title {
        font-size: 24px;
    }
}

@media screen and (max-height: 600px) and (min-width: 425px) and (max-width: 768px) {
    .bottom-title {
        font-size: 20px;
    }
}

@media screen and (max-height: 450px) and (min-width: 425px) and (max-width: 768px) {
    .bottom-title {
        font-size: 18px;
    }
}

.bottom-title-100 {
    position: absolute;
    top: 50%;
    display: block;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
}