.my-projects-list-item {
    position: relative;
    display: block;
    top: 0;
    left: 0;
    margin: 90px auto 0;
    width: 750px;
    height: 400px;
    overflow: hidden;
    cursor: initial;
    box-shadow: 0 20px 80px 0 #000000;
    border: 4px solid transparent;
    border-radius: 3px;
    opacity: 0;
    visibility: hidden;
}

.first-project {
    top: 0;
    margin: 60px auto !important;
}

.my-projects-list-item:nth-child(8) {
    margin-bottom: 0;
}

.left-translation {
    transform: translateX(-12%);
}

.right-translation {
    transform: translateX(12%);
}

.my-projects-image {
    position: relative;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transform: scale(1);
    transform-origin: center;
    transition: transform 500ms cubic-bezier(.075,.82,.165,1), filter 500ms cubic-bezier(.075,.82,.165,1);
}

.project-description-main-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    transform: scaleX(0);
    transform-origin: left center;
    background: linear-gradient(90deg, #141414  0%, #0a0a0a 35%, #000000 100%);
    color: white;
    transition: transform 500ms cubic-bezier(.175,.885,.32,1.275);
    opacity: .7;
    overflow: hidden;
}

.prepare {
    transform: scaleX(1);
}

.my-project-name {
    position: absolute;
    top: 5px;
    left: 0;
    color: #ffffff;
    width: 100%;
    text-align: center;
    font-size: 28px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 800;
    letter-spacing: 2px;
}

.project-description-container {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.my-project-description {
    position: relative;
    top: 0;
    font-size: 18px;
    padding: 0 15px;
    font-weight: 400;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 2px;
}

.my-project-open-btn {
    position: relative;
    margin: 16px 0 0 0;
    top: 0;
    left: 0;
    border: none;
    outline: none;
    color: white;
    font-size: 14px;
    padding: 10px 40px;
    font-family: 'Montserrat', sans-serif;
    background: linear-gradient(90deg, #465986 0%, #2e4883 50%, #465986 100%);
    border-radius: 2px;
    transition: background-position 500ms ease;
}

.my-project-open-btn:hover {
    background-position: 300px; 
}

.my-projects-list-item:hover .my-projects-image {
    transform: scale(1.5);
    filter: brightness(0.75) blur(2.5px);  
}

.my-projects-list-item:hover .project-description-main-container {
    transform: scaleX(1); 
}

.disabled-link {
    pointer-events: none;
    cursor: default;
}

.enabled-link {
    pointer-events: all;
    cursor: pointer;
}

