.loading-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    transform: translate3d(0, 0, 0);
    background-color: #6b6463;
    opacity: 1;
  }

  #loading-description {
    position: relative;
    top: 0;
    left: 0;
    font-size: 16px;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
    text-align: center;
    color: #e4ba94;
    margin-top: 12px;
    padding: 0 25px;
    text-align: center;
  }

  #loader-value {
    display: inline-block;
    position: absolute;
    font-size: 16px;
    color: #000000;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }

  .loader {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 35px;
    background-color: #e4ba94;
    padding: 2px;
    overflow: hidden;
    border-radius: 2px;
  }

  .loader-part {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: scaleX(0);
    transform-origin: left center;
    transition: transform 150ms ease;
    background-color: #6b6463;
    border-radius: 2px;
  }

  .loader-container {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 60%;
    height: auto;
    max-width: 650px;
    text-align: center;
  }

.hide {
  animation: remove-loading-container 1000ms 4600ms cubic-bezier(1, 0, 0.39, 1.54) both 1;
}

@keyframes remove-loading-container {
  0% {transform: translate3d(0, 0, 0);}
  100% {transform: translate3d(-105%, 0, 0);}
}

@media screen and (max-width: 768px) {
  #loading-description, #loader-value {
    font-size: 14px;
  }

  .loader {
    height: 30px;
  }

  #loading-description {
    margin-top: 10px;
  }
}

@media screen and (max-width: 425px) {
  #loading-description, #loader-value {
    font-size: 12px;
  }

  .loader {
    height: 25px;
  }

  #loading-description {
    margin-top: 8px;
  }
}