.main-scene-container {
  position: relative;
  top: 0;
  left: 0;
  width: auto;
  height: auto;
  margin: 0;
  padding: 0;
  opacity: 1;
  overflow: hidden;
}

#c {
    position: relative;
    top: 0;
    left: 0;
    width: 700px;
    height: 100%;
    margin: 0;
    padding: 0;
    z-index: 9;
    visibility: hidden;
    transform: translate3d(150%, 0, 0);
  }

  .show-c1 {
    animation: show-model 1s cubic-bezier(1, 0, 0.39, 1.54) both 1;
  }

  @keyframes show-model {
    0% {visibility: visible; transform: translate3d(150%, 0, 0);}
    100% {visibility: visible; transform: translate3d(0, 0, 0);}
  }
  
  @media screen and (max-width: 768px) {
    .main-scene-container {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }

  .canvas-width-100 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }