@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap');

html {
  overflow: hidden;
}

html, body {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: #6b6463;
  touch-action: pan-x pan-y;
}

p, span, div, h1, h2, h3, a {
  -webkit-text-size-adjust:100%;
}

button:focus {
  border: none;
  outline: none;
}

.show-el {
  animation: show-an 500ms cubic-bezier(0.25, 0.46, 0.45, 0.94) both 1;
}

.show-left-translation-anim {
  animation: left-translation-an 500ms cubic-bezier(0.25, 0.46, 0.45, 0.94) both 1;
}

.show-right-translation-anim {
  animation: right-translation-an 500ms cubic-bezier(0.25, 0.46, 0.45, 0.94) both 1;
}

@keyframes show-an {
  0% { visibility: visible; opacity: 0; transform: translate3d(0px, 100%, 0px); }
  100% { visibility: visible; opacity: 1; transform: translate3d(0px, 0px, 0px); top: 0; }
}

@keyframes left-translation-an {
  0% { visibility: visible; opacity: 0; transform: translate3d(-12%, 100%, 0px); }
  100% { visibility: visible; opacity: 1; transform: translate3d(-12%, 0px, 0px); top: 0; }
}

@keyframes right-translation-an {
  0% { visibility: visible; opacity: 0; transform: translate3d(12%, 100%, 0px); }
  100% { visibility: visible; opacity: 1; transform: translate3d(12%, 0px, 0px); top: 0; }
}

@keyframes left-translation-an-mob {
  0% { visibility: visible; opacity: 0; transform: translate3d(0px, 100%, 0px); }
  100% { visibility: visible; opacity: 1; transform: translate3d(0px, 0px, 0px); top: 0; }
}

@keyframes right-translation-an-mob {
  0% { visibility: visible; opacity: 0; transform: translate3d(0px, 100%, 0px); }
  100% { visibility: visible; opacity: 1; transform: translate3d(0px, 0px 0px); top: 0; }
}

.background {
  position: fixed;
  width: 100%;
  height: 100%;
  background: url(../src/images/Missing_dev.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  -webkit-background-size: cover;
  filter: brightness(.5) blur(2px);
  -webkit-filter: brightness(.5) blur(2px);
  z-index: 1;
}

.main {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 999999;
  overflow-x: hidden;
  touch-action: pan-x pan-y;
  pointer-events: none;
}

.enable-pointer-events {
  pointer-events: all;
}

.smooth-scroll {
  scroll-behavior: smooth;
}

.main-top-wrapper, .top-wrapper {
  position: relative;
  top: 0;
  left: 0;
  width: 100vw;
  margin: 0;
  padding: 0;
}

.main-top-wrapper {
  height: auto;
}

.top-wrapper {
  height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  opacity: 0;
}

canvas {
  touch-action: initial !important;
}

body {
  overflow-y: hidden;
}

button {
  cursor: pointer;
}

.App {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #6b6463;
}

h1, h2, h3, h4, h5, h6, p, ul, li {
  padding: 0;
  margin: 0;
}

/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #e4ba94 #6b6463;
}

/* Chrome, Edge and Safari */
*::-webkit-scrollbar {
  width: 10px;
  width: 10px;
}
*::-webkit-scrollbar-track {
  border-radius: 0px;
  background-color: #6b6463;
}

*::-webkit-scrollbar-track:hover {
  background-color: #55504f;
}

*::-webkit-scrollbar-track:active {
  background-color: #e4ba94;
}

*::-webkit-scrollbar-thumb {
  border-radius: 0px;
  background-color: #e4ba94;
}

*::-webkit-scrollbar-thumb:hover {
  background-color: #b79985;
}

*::-webkit-scrollbar-thumb:active {
  background-color: #6b6463;
}

@media screen and (max-width: 1440px) {
  *::-webkit-scrollbar {
    width: 8px;
    width: 8px;
  }
}

@media screen and (max-width: 768px) {
  .background {
    filter: brightness(.5) blur(1.5px);
    -webkit-filter: brightness(.5) blur(1.5px);
  }

  .show-left-translation-anim {
    animation: left-translation-an-mob 500ms cubic-bezier(0.25, 0.46, 0.45, 0.94) both 1;
  }
  
  .show-right-translation-anim {
    animation: right-translation-an-mob 500ms cubic-bezier(0.25, 0.46, 0.45, 0.94) both 1;
  }

  *::-webkit-scrollbar {
    width: 7px;
    width: 7px;
  }
}

@media screen and (max-width: 500px) {
  *::-webkit-scrollbar {
    width: 6px;
    width: 6px;
  } 
}

@media screen and (max-height: 768px) {
  .main-top-wrapper {
    padding-bottom: 45px;
  }
}

@media screen and (max-height: 220px) {
  .main-top-wrapper {
    padding-bottom: 60px;
  }
}