.main-title-container {
    width: 40%;
    max-width: 1000px;
    position: relative;
    display: inline-block;
    top: 0;
    left: 0;
    font-family: 'Montserrat', sans-serif;
    background-color: transparent;
    vertical-align: middle;
    z-index: 99;
    color: white;
    opacity: 1;
  }
  
  .title-container {
    position: relative;
    display: block;
    width: 100%;
    top: 0;
    left: 0;
    text-align: center;
    margin: 0;
    opacity: 0;
    transition: opacity 1s cubic-bezier(0, -0.36, 0.4, 0.99);
  }

  .show-main-title {
    opacity: 1;
  }
  
  #main-title {
    position: relative;
    margin: 0;
    padding: 0;
    font-size: 54px;
    top: 0;
    left: 0;
    padding: 0 25px;
  }
  
  #main-description {
    position: relative;
    margin: 10px 0 0 0;
    padding: 0 12px;
    top: 0;
    left: 0;
    font-size: 20px;
  }

  @media screen and (max-width: 1680px) {
    #main-title {
      font-size: 46px;
    }
  
    #main-description {
      font-size: 18px;
    }
  }

    @media screen and (max-width: 1280px) {
    #main-title {
      font-size: 42px;
    }
  
    #main-description {
      font-size: 18px;
    }
  }
  
  @media screen and (max-width: 1024px) {
    #main-title {
      font-size: 36px;
    }
  
    #main-description {
      font-size: 16px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .main-title-container {
        width: 100%;
        left: 0;
    }

    #main-title {
      font-size: 32px;
    }

    #main-description {
      font-size: 14px;
    }
  }

  @media screen and (max-width: 445px) {
    #main-title {
      font-size: 28px;
    }

    #main-description {
      font-size: 14px;
    }
  }

  @media screen and (max-width: 220px) {
    #main-title {
      font-size: 24px;
    }

    #main-description {
      font-size: 12px;
    }
  }

  @media screen and (max-height: 750px) and (min-width: 768px) {
    #main-title {
      font-size: 36px;
    }

    #main-description {
      font-size: 16px;
    }
  }

  @media screen and (max-height: 600px) and (min-width: 768px) {
    #main-title {
      font-size: 32px;
    }

    #main-description {
      font-size: 14px;
    }
  }

  @media screen and (max-height: 450px) and (min-width: 768px) {
    #main-title {
      font-size: 28px;
    }

    #main-description {
      font-size: 14px;
    }
  }

  @media screen and (max-height: 750px) and (max-width: 768px) {
    #main-title {
      font-size: 32px;
    }

    #main-description {
      font-size: 14px;
    }
  }

  @media screen and (max-height: 600px) and (max-width: 768px) {
    #main-title {
      font-size: 28px;
    }

    #main-description {
      font-size: 14px;
    }
  }

  @media screen and (max-height: 450px) and (max-width: 768px) {
    #main-title {
      font-size: 24px;
    }

    #main-description {
      font-size: 12px;
    }
  }

  .main-title-70 {
    width: 70%;
    left: 0;
  }