.my-projects-container {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    margin: 0;
    margin: 0;
}

.my-projects-title {
    position: relative;
    background-color: transparent;
    font-size: 36px;
    color: white;
    font-family: 'Montserrat', sans-serif;
    opacity: 0;
    top: 0;
    padding: 15px 0;
    letter-spacing: 3px;
}

.my-projects-list {
    position: relative;
    padding: 0;
    top: 0;
    left: 0;
    width: 100%;
    min-width: 190px;
    font-family: 'Montserrat', sans-serif;
}

@media screen and (max-width: 900px) {
    .my-projects-container {
        margin: 60px 0 0 0;
    }
}

@media screen and (max-width: 820px) {
    .my-projects-title {
        font-size: 32px;
    }
}

@media screen and (max-width: 768px) {
    .my-projects-container {
        margin: 50px 0 0 0;
    }

    .my-projects-title {
        letter-spacing: 2px;
    }
}

@media screen and (max-width: 660px) {
    .my-projects-title {
        font-size: 26px;
    }
}

@media screen and (max-width: 600px) {
    .my-projects-container {
        margin: 40px 0 0 0;
    }
}

@media screen and (max-width: 535px) {
    .my-projects-title {
        font-size: 24px;
    }
}

@media screen and (max-width: 445px) {
    .my-projects-container {
        margin: 30px 0 0 0;
    }
}

@media screen and (max-width: 440px) {
    .my-projects-title {
        font-size: 22px;
    }
}

@media screen and (max-width: 360px) {
    .my-projects-title {
        font-size: 20px;
    }
}

@media screen and (max-width: 300px) {
    .my-projects-title {
        font-size: 18px;
    }

    .my-projects-container {
        margin: 20px 0 0 0;
    }
}

@media screen and (max-width: 200px) {
    .my-projects-title {
        font-size: 16px;
    }
}

@media screen and (max-height: 750px) and (min-width: 375px) {
    .my-projects-title {
        font-size: 28px;
    }
}

@media screen and (max-height: 600px) and (min-width: 375px) {
    .my-projects-title {
        font-size: 24px;
        letter-spacing: 2px;
    }
}

@media screen and (max-height: 450px) and (min-width: 375px) {
    .my-projects-title {
        font-size: 20px;
    }
}

@media screen and (max-height: 250px) {
    .my-projects-title {
        padding-top: 30px;
    }
}