.footer {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    padding: 0;
    opacity: 1;
    padding: 0 0 15px 0;
}

.contacts-content-container {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
}

.contacts-no-canvas {
    margin-top: 0;
}

.contacts-content-child-container {
    position: relative;
    flex: 1 1 0;
    max-width: 200px;
    top: 0;
    left: 0;
    margin: 0 75px;
    transition: transform 250ms ease-in-out;
    cursor: pointer;
}

.contacts-content-child-container:hover {
    transform: scale(1.15);
}

.contacts-content-child-container:hover .contacts-icon {
    transform: rotate(360deg);
}

.contacts-content-child-container:hover .contacts-value {
    color: white;
}

.contacts-link {
    text-decoration: none;
}

.contacts-icon {
    width: 60px;
    height: 60px;
    transition: transform 500ms ease;
}

.contacts-value {
    display: block;
    color: #c0c0c0;
    font-family: 'Montserrat', sans-serif;
    margin-top: 2px;
    transition: color 250ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.more-projects-container {
    margin-top: 20px;
    font-family: 'Montserrat', sans-serif;
    color: white;
}

.more-projects-text, .more-projects-link, .contacts-value {
    font-size: 16px;
}

.more-projects-text, .more-projects-link {
    letter-spacing: 1px;
}

.more-projects-link {
    color: #e4ba94;
    padding: 0 5px;
    text-decoration: none;
    transition: color 150ms ease-in-out;
}

.more-projects-link:hover {
    color: #968272;
}

.fotter-bottom-text {
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    margin-top: 10px;
    letter-spacing: 1px;
}

#smiley {
    display: inline-block;
    vertical-align: middle;
    margin: 0 0 0 2px;
    width: 16px;
    height: 16px;
    animation: rotation 1500ms cubic-bezier(0.880, -0.660, 0.265, 1.420) infinite;
}

@keyframes rotation {
    0% { transform: rotate(0deg); }   
    100% { transform: rotate(360deg); }
}

@media screen and (max-width: 1440px) {
    .contacts-icon {
        width: 50px;
        height: 50px;
    }

    .more-projects-text, .more-projects-link, .contacts-value {
        font-size: 14px;
    }

    .contacts-content-child-container {
        margin: 0 45px;
        max-width: 180px;
    }

    .fotter-bottom-text {
        font-size: 12px;
    }

    #smiley {
        width: 14px;
        height: 14px;
    }

    .footer {
        padding: 0 0 15px 0;
    }
}

