.settings-main-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, #111 25%, #000 100%);
    z-index: 999;
    pointer-events: all;
}

.settings-container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 310px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transform-origin: center center;
    background-color: transparent;
}

.switch-btn-container {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    margin: 10px 0 0 0;
    justify-content: space-evenly;
}

.switch-btn-description {
    position: relative;
    top: 0;
    left: 0;
    color: #646464;
    white-space: nowrap;
    height: auto;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    flex-basis: calc(100% - 70px);
}

.switch-btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    width: 50px;
    height: 25px;
    background-color: black;
    border: 0.5px solid #646464;
    padding: 1px;
    border-radius: 50px;
    overflow: hidden;
}

.switch-circle {
    position: relative;
    top: 0;
    left: 0;
    width: 24px;
    height: 24px;
    background-color: #f2f2f2;
    border-radius: 50%;
    transition: transform 250ms ease;
    transform: translateX(0px);
    z-index: 99;
    border: none;
    padding: 0;
}

.switch-btn-off {
    position: absolute;
    margin: 0;
    padding: 0;
    top: 50%;
    left: 100%;
    transform: translateY(-50%) translateX(-180%);
    width: 8px;
    height: 8px;
    background-color: transparent;
    border: 0.5px solid #646464;
    border-radius: 50%;
}

.switch-btn-on {
    position: absolute;
    margin: 0;
    padding: 0;
    top: 50%;
    left: 0;
    transform: translateY(-50%) translateX(100%);
    width: 10px;
    height: 10px;
    background-color: transparent;
    border-left: 0.5px solid #f2f2f2;
}

.settings-info-btn {
    position: relative;
    top: 0;
    left: 0;
    width: auto;
    height: auto;
    background-color: transparent;
    color: #a394e7;
    border: none;
    text-align: center;
    margin: 15px 0 0 0;
    transition: color 150ms ease-out;
    font-family: 'Montserrat', sans-serif;
    font-size: 13px;
    padding: 0;
}

.run-the-app-btn, .what-is-that-btn {
    position: relative;
    top: 0;
    left: 0;
    width: auto;
    height: auto;
    margin: 20px 0 0 0;
    padding: 5px;
    background-color: transparent;
    border: none;
    border-radius: 3px;
    color: #646464;
    transition: border-color 250ms ease, color 250ms ease;
    font-family: 'Montserrat', sans-serif;
    font-size: 15px;
    font-weight: bold;
}

.what-is-that-container {
    position: relative;
    margin: 0;
    padding: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transform-origin: center center;
    width: 400px;
    height: auto;
    display: none;
}

#what-is-that-description {
    position: relative;
    padding: 3px 6px;
    margin: 10px 0 0 0;
    top: 0;
    left: 0;
    font-size: 16px;
    letter-spacing: 3px;
    font-family: 'Montserrat', sans-serif;
    color: white;
}

#what-is-that-title {
    color: red;
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
}

.run-the-app-btn:hover, .what-is-that-btn:hover {
    color: #aaaaaa;
}

.settings-info-btn:hover {
    color: #6557a5;
}

.show-block {
    display: block;
}

.hide-block {
    display: none;
}

.circle-enabled {
    transform: translateX(26px);
}

.switch-btn-enabled {
    background: #52c547;
}

.setting-enabled {
    color: #f2f2f2;
}

@media screen and (max-width: 1440px) {
    .settings-container {
        width: 260px;
    }

    .switch-btn-description {
        font-size: 14px;
        flex-basis: calc(100% - 60px);
    }

    .run-the-app-btn {
        font-size: 13px;
    }

    .settings-info-btn {
        font-size: 12px;
    }

    .switch-btn {
        width: 40px;
        height: 20px;
    }

    .switch-btn-on {
        width: 8px;
        height: 8px;
    }

    .switch-btn-off {
        width: 6px;
        height: 6px;
    }

    .switch-circle {
        width: 19px;
        height: 19px;
    }

    .circle-enabled {
        transform: translateX(21px);
    }

    #what-is-that-title {
        font-size: 20px;
    }

    #what-is-that-description {
        font-size: 14px;
    }
}

