.about-me-container {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    font-family: 'Montserrat', sans-serif;
    color: white;
    margin: 45px 0 0 0;
    z-index: 9;
    opacity: 0;
}

.about-me-title-container {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 1300px;
    margin: 0 auto;
}

#about-me-title {
    font-size: 38px;
    padding: 0 10px;
    letter-spacing: 1px;
}

.about-me-description {
    font-size: 20px;
    display: inline-block;
    width: 70%;
    margin: 10px 0 0 0;
    letter-spacing: 1px;
}

@media screen and (max-width: 1440px) {
    #about-me-title {
        font-size: 36px;
    }
    
    .about-me-description {
        font-size: 18px;
    }
}

@media screen and (max-width: 1024px) {
    #about-me-title {
        font-size: 32px;
    }
    
    .about-me-description {
        font-size: 18px;
    }
}

@media screen and (max-width: 768px) {
    .about-me-container {
        margin: 30px 0 0 0;
    }

    #about-me-title {
        font-size: 28px;
    }
  
    .about-me-description {
        font-size: 16px;
    }
  }

  @media screen and (max-width: 445px) {
    #about-me-title {
        font-size: 24px;
    }
  
    .about-me-description {
        font-size: 14px;
    }
  }

  @media screen and (max-width: 320px) {
    #about-me-title {
        font-size: 18px;
    }
  
    .about-me-description {
        font-size: 12px;
    }
  }

  @media screen and (max-height: 768px) and (min-width: 768px) {
    #about-me-title {
        font-size: 32px;
    }
  
    .about-me-description {
        font-size: 18px;
    }
  }

  @media screen and (max-height: 600px) and (min-width: 768px) {
    #about-me-title {
        font-size: 28px;
    }
  
    .about-me-description {
        font-size: 16px;
    }
  }

  @media screen and (max-height: 450px) and (min-width: 768px) {
    #about-me-title {
        font-size: 24px;
    }
  
    .about-me-description {
        font-size: 12px;
    }
  }

  @media screen and (max-height: 768px) and (max-width: 768px) {
    #about-me-title {
        font-size: 24px;
    }
  
    .about-me-description {
        font-size: 12px;
    }
  }

  @media screen and (max-height: 600px) and (max-width: 768px) {
    #about-me-title {
        font-size: 20px;
    }
  
    .about-me-description {
        font-size: 10px;
    }
  }

  @media screen and (max-height: 450px) and (max-width: 768px) {
    #about-me-title {
        font-size: 18px;
    }
  
    .about-me-description {
        font-size: 9px;
    }
  }


  
