nav {
    position: fixed;
    top: 15px;
    left: 75%;
    transform: translateX(-75%);
    z-index: 999;
    font-family: 'Montserrat', sans-serif;
    white-space: nowrap;
  }

  .nav-list {
    display: block;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  li {
    display: inline;
    padding: 0;
    cursor: pointer;
  }

  .navlink {
    text-decoration: none;
    color: white;
    transition: color 150ms ease-out, font-size 150ms ease-in, opacity 500ms cubic-bezier(0, -0.36, 0.4, 0.99), text-shadow 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
    font-size: 24px;
    margin: 0 8px;
    opacity: 0;
    letter-spacing: 1px;
  }

  .navlink:hover {
    color: #e4ba94;
    font-size: 28px;
  }

  .show-text {
    opacity: 1;
  }

  .selected-menu-item {
    text-shadow: 2px 2px 4px #c5c1ba, -2px 2px 4px #c5c1ba, 2px -2px 4px #c5c1ba, -2px -2px 4px #c5c1ba;
  }

  .selected-menu-item:hover {
    text-shadow: 2px 2px 4px #b89373, -2px 2px 4px #b89373, 2px -2px 4px #b89373, -2px -2px 4px #b89373;
  }

    @media screen and (max-width: 1440px) {
      .navlink {
        font-size: 20px;
      }

      .navlink:hover {
        font-size: 24px;
      }
  }

  @media screen and (max-width: 1024px) {
    .navlink {
      font-size: 18px;
    }

    .navlink:hover {
      font-size: 20px;
    }
}

  
  @media screen and (max-width: 768px) {
    nav {
      left: 50%;
      transform: translateX(-50%);
      top: 10px;
    }
  
    .navlink {
      font-size: 16px;
      margin: 0 5px;
    }

    .navlink:hover {
      font-size: 18px;
    }
  }

  @media screen and (max-width: 425px) {
    .navlink {
      font-size: 14px;
    }

    .navlink:hover {
      font-size: 16px;
    }

    nav {
        top: 8px;
    }
  }

